import Notification from './notification.js';

let messageInstance;

function getMessageInstance() {
  messageInstance = messageInstance || Notification.newInstance();
  // alert
  return messageInstance;
}

/**
 * @param {String} type 
 */
const iconTypes = {
  'info': 'icon-alert-info',
  'success': 'icon-alert-success',
  'warning': 'icon-alert-warning',
  'error': 'icon-alert-error',
  'loading': 'icon-alert-loading'
}

/**

 * 

 * @method notice 

 * @for 

 * @param {String} content 

 * @param {Number} duration  1.5s

 * @param {Boolean} closable  false

 * @param {String} type 

 * @param {String} position top

 * @return {} 

 */

function notice(content = '', duration = 1.5, closable = false, type) {
  const iconType = iconTypes[type]
  let instance = getMessageInstance();

  instance.add({
    content: content,
    duration: duration,
    closable: closable,
    type: iconType
  });
}

export default {
  info (options) {
    return this.message('info', options)
  },
  success (options) {
    return this.message('success', options)
  },
  warning (options) {
    return this.message('warning', options)
  },
  error (options) {
    return this.message('error', options)
  },
  loading (options) {
    return this.message('loading', options)
  },
  message (type, options) {
    if (typeof options === 'string') {
      // ,
      options = { content: options }
    };
    return notice(options.content, options.duration, options.closable, type)
  }
}


