import router from './router'

router.beforeEach((to, from, next) => {
  // 
  var userToken = localStorage.getItem('welcome');
  if (userToken) {
    // Token 
    if (to.path == '/login') {
      // login 
      next('/')
    } else {
      next()
    }
  } else {
    // Token 
    if (to.path == '/login') {
      next();
    } else {
      next('/login');
    }
  }

})


